import { h, customElement, Any, useHost, useEvent, useEffect } from "atomico";

import style from "./a-router.css";

const ARouterCase = ({ src, path }) => {
  let refHost = useHost();

  let dispatchChangeSrc = useEvent("ChangeARouterCaseSrc", { bubbles: true });
  let dispatchRemoveSrc = useEvent("RemoveARouterCaseSrc", { bubbles: true });

  useEffect(() => {
    let { current } = refHost;
    dispatchChangeSrc({ src, path });
    return () => {
      !current.isConnected && dispatchRemoveSrc();
    };
  }, [src]);

  return <host></host>;
};

const ARouterSwitch = () => {
  let ref = useHost();
  return (
    <host
      shadowDom
      onChangeARouterCaseSrc={({ target: { src, path } }) => {}}
      onRemoveARouterCaseSrc={({ target: { src, path } }) => {
        console.log({ src, path });
      }}
    >
      <style>{style}</style>
    </host>
  );
};

ARouterCase.props = {
  path: {
    type: String,
    reflect: true
  },
  src: {
    type: Any
  }
};

customElement("a-router-switch", ARouterSwitch);
customElement("a-router-case", ARouterCase);
